import { useObserver } from 'mobx-react-lite'
import React from 'react'

interface IObserverWhenProps {
  test(): boolean
  renderSuccess(): ReactNode
  renderFailure?(): ReactNode
}

export const ObserverWhen: React.FC<IObserverWhenProps> = ({
  test,
  renderSuccess,
  renderFailure,
}) =>
  useObserver(
    () => (test() ? renderSuccess() : renderFailure ? renderFailure() : null),
  )
